/*
Image and Video base class
*/

// import { processVideo } from './utils/imageUtilities';

class Video {
  constructor(video, size) {
    this.videoElt = null;
    this.size = size;
    this.videoReady = false;
    // this.onVideoReady = () => {
    //   this.videoReady = true;
    //   if (this.waitingPredictions) {
    //     this.waitingPredictions.forEach(i => this.predict(i.imgToPredict, i.num, i.callback));
    //   }
    // };

    if (video instanceof HTMLVideoElement) {
      this.videoElt = video;
      // this.video = this.loadVideo(video, this.imageSize, this.onVideoReady);
    } else if (typeof video === 'object' && video.elt instanceof HTMLVideoElement) {
      // Handle p5.js video element
      this.videoElt = video.elt;
      // this.video = this.loadVideo(video.elt, this.imageSize, this.onVideoReady);
    } // else {
    //   this.videoReady = true;
    // }

    // this.loadVideo();
  }

  loadVideo() {
    return new Promise((resolve) => {
      this.video = document.createElement('video');
      this.video.srcObject = this.videoElt.captureStream();
      this.video.width = this.size;
      this.video.height = this.size;
      this.video.autoplay = true;
      this.video.playsinline = true;
      this.video.muted = true;
      resolve();
    });
  }
}

export default Video;
